<template>
  <div class="login">
    <div class="formBox">
      <div class="h5">
        <div class="hello">Hello，欢迎登录</div>
        <div class="xt">筷赚商家后台系统</div>
      </div>
      <!-- <div class="flex-row-center h5">
        <img class="logo" src="../../assets/images/logo/logo.png" alt="">
        <div class="title">筷赚商家后台管理系统</div>
      </div> -->
      <div class="form">
        <!-- 密码 -登录框 -->
        <div class="loginForm" v-show="type == 'passlogin'">
          <el-form ref="passloginForm" :model="passloginForm" :rules="rules" label-position="left" label-width="0px" class="login-form">
            <el-form-item prop="phone">
              <el-input v-model="passloginForm.phone" autocomplete="off" placeholder="请输入手机号">
                <img slot="prefix" src="../../assets/images/login/sjh.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item prop="loginpassword">
              <el-input v-model="passloginForm.loginpassword" type="password" auto-complete="new-password" autocomplete="off" placeholder="请输入密码">
                <img slot="prefix" src="../../assets/img/password.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <!-- 拖图验证 -->
            <el-form-item v-show="codeUrlsfy">
              <template slot-scope="scope">
                <div class="codestf" :data-type="scope">
                  <slide-verify :l="42" :r="10" :w="350" :h="155" @success="onSuccess" @fail="onFail" @refresh="onRefresh" :slider-text="text"></slide-verify>
                </div>
              </template>
            </el-form-item>
            <el-form-item v-show="!codeUrlsfy" style="text-align:right;">
              <el-checkbox style="margin-right:20px;" v-model="passloginForm.rememberMe">记住密码</el-checkbox>
              <el-button type="text" @click="changeType('resetpass')">忘记密码</el-button>
            </el-form-item>
            <el-form-item style="width:100%;margin-top:0px;text-align:center;" :style="{ 'margin-top': codeUrlsfy ? '65px' : '0'}">
              <el-button style="width:198px;" :loading="loginLoading" size="medium" type="primary" @click="passLoginFun('passloginForm')">
                <span v-if="!loginLoading">登 录</span>
                <span v-else>登 录 中...</span>
              </el-button>
            </el-form-item>
            <el-form-item v-show="!codeUrlsfy" style="width:100%;margin-top:0px;text-align:center;">
              <el-button type="text" @click="changeType('codelogin')">验证码登录</el-button>
              <span style="color:#2B8BFF;margin:0 10px;font-size:18px;">|</span>
              <el-button type="text" @click="changeType('logon')">免费注册</el-button>
            </el-form-item>
          </el-form>
        </div>
        <!-- 验证码 - 登录框 -->
        <div class="loginForm" v-show="type == 'codelogin'">
          <el-form ref="codeloginForm" :model="codeloginForm" :rules="rules" label-position="left" label-width="0px" class="login-form">
            <el-form-item prop="phone">
              <el-input v-model="codeloginForm.phone" autocomplete="off" placeholder="请输入手机号">
                <img slot="prefix" src="../../assets/images/login/sjh.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item prop="code">
              <el-input v-model="codeloginForm.code" auto-complete="new-password" autocomplete="off" placeholder="请输入验证码" style="width: 210px">
                <img slot="prefix" src="../../assets/images/login/yzm.png" class="el-input__icon input-icon">
              </el-input>
              <el-button icon="el-icon-mobile-phone" @click="sendLogin" style="width: 120px" type="primary" :disabled="disabled=!show" >  
                <span v-show="codeloginshow">获取验证码</span>
                <span v-show="!codeloginshow" class="count">{{codelogincount}} s</span>
              </el-button>
            </el-form-item>
            <el-form-item style="width:100%;margin-top:0px;text-align:center;">
              <el-button style="width:198px;" :loading="loginLoading" size="medium" type="primary" @click.native.prevent="codeLoginFun('codeloginForm')">
                <span v-if="!loginLoading">登 录</span>
                <span v-else>登 录 中...</span>
              </el-button>
            </el-form-item>
            <el-form-item style="width:100%;margin-top:0px;text-align:center;">
              <el-button type="text" @click="changeType('passlogin')">密码登录</el-button>
              <span style="color:#2B8BFF;margin:0 10px;font-size:18px;">|</span>
              <el-button type="text" @click="changeType('logon')">免费注册</el-button>
            </el-form-item>
          </el-form>
        </div>
        <!-- 注册框 -->
        <div class="logonForm" v-show="type == 'logon'">
          <el-form ref="logonform"
            :rules="rules"
            :model="logonform" label-position="left" label-width="0px" class="login-form">
            <el-form-item prop="username">
              <el-input class="flex-row-start" v-model="logonform.username" autocomplete="off" placeholder="请输入用户名">
                <img slot="prefix" src="../../assets/images/login/yhm.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item prop="cellPhone">
              <el-input v-model="logonform.cellPhone" autocomplete="off" placeholder="请输入手机号">
                <img slot="prefix" src="../../assets/images/login/sjh.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item prop="code">
              <el-input v-model="logonform.code" autocomplete="off" placeholder="请输入验证码" style="width: 210px">
                <img slot="prefix" src="../../assets/images/login/yzm.png" class="el-input__icon input-icon">
              </el-input>
              <el-button icon="el-icon-mobile-phone" @click="send" style="width: 120px" type="primary" :disabled="disabled=!show" >  
                <span v-show="show">获取验证码</span>
                <span v-show="!show" class="count">{{count}} s</span>
              </el-button>
            </el-form-item>
            <el-form-item prop="password">
              <el-input v-model="logonform.password" type="password" show-password auto-complete="new-password" autocomplete="off" placeholder="请输入密码，6-12位字符">
                <img slot="prefix" src="../../assets/images/login/mm.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item prop="passwordAgain" style="margin-bottom:30px;">
              <el-input v-model="logonform.passwordAgain" type="password" show-password auto-complete="new-password" autocomplete="off" placeholder="请再次输入">
                <img slot="prefix" src="../../assets/images/login/mm.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item style="width:100%;margin-top:0px;text-align:center;">
              <el-button :loading="logonLoading" size="medium" type="primary" style="width:198px;" @click.native.prevent="handleLogon('logonform')">注册</el-button>
            </el-form-item>
            <el-form-item style="width:100%;margin-top:0px;text-align:center;">
              <el-button type="text" @click="changeType('passlogin')">已有账号，立即登录</el-button>
            </el-form-item>
          </el-form>
        </div>
        <!-- 忘记密码框 -->
        <div class="logonForm" v-show="type == 'resetpass'">
          <el-form ref="resetpassform"
            :rules="rules"
            :model="resetpassform" label-position="left" label-width="0px" class="login-form">
            <el-form-item prop="cellPhone">
              <el-input v-model="resetpassform.cellPhone" autocomplete="off" placeholder="请输入手机号">
                <img slot="prefix" src="../../assets/images/login/sjh.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item prop="code">
              <el-input v-model="resetpassform.code" autocomplete="off" placeholder="请输入验证码" style="width: 210px">
                <img slot="prefix" src="../../assets/images/login/yzm.png" class="el-input__icon input-icon">
              </el-input>
              <el-button icon="el-icon-mobile-phone" @click="sendreset" style="width: 120px" type="primary" :disabled="disabled=!resetpassshow" >  
                <span v-show="resetpassshow">获取验证码</span>
                <span v-show="!resetpassshow" class="count">{{resetpasscount}} s</span>
              </el-button>
            </el-form-item>
            <el-form-item prop="resetpassword">
              <el-input v-model="resetpassform.resetpassword" type="password" show-password auto-complete="new-password" autocomplete="off" placeholder="请输入密码，6-12位字符">
                <img slot="prefix" src="../../assets/images/login/mm.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item prop="resetwordAgain" style="margin-bottom:30px;">
              <el-input v-model="resetpassform.resetwordAgain" type="password" show-password auto-complete="new-password" autocomplete="off" placeholder="请再次输入">
                <img slot="prefix" src="../../assets/images/login/mm.png" class="el-input__icon input-icon">
              </el-input>
            </el-form-item>
            <el-form-item style="width:100%;margin-top:0px;text-align:center;">
              <el-button :loading="resetLoading" size="medium" type="primary" style="width:198px;" @click.native.prevent="handleReset('resetpassform')">重置密码</el-button>
            </el-form-item>
            <el-form-item style="width:100%;margin-top:0px;text-align:center;">
              <el-button type="text" @click="changeType('passlogin')">已有账号，立即登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Config from '@/config'
import { setToken } from '@/utils/auth'
import {
  sendPhoneCode,
  checkCode,
  userRegist,
  login,
  editPassWord
} from '@/api/login/logon'
import Cookies from 'js-cookie'
let TIME_COUNT = 60; //更改倒计时时间
let LOGIN_TIME_COUNT = 60; //更改倒计时时间
let RESET_TIME_COUNT = 60; //更改倒计时时间

export default {
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.logonform.passwordAgain !== '') {
          this.$refs.logonform.validateField('passwordAgain');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.logonform.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    var validatePass3 = (rule, value, callback) => {
      console.log(value)
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.resetpassform.resetwordAgain !== '') {
          this.$refs.resetpassform.validateField('resetwordAgain');
        }
        callback();
      }
    };
    var validatePass4 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.resetpassform.resetpassword) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'));
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
        console.log(reg.test(value));
        if (reg.test(value)) {
          callback();
        } else {
          return callback(new Error('请输入正确的手机号'));
        }
      }
    };
    return {
      codeUrl: '',
      cookiePass: '',
      token: '',
      codeUrlsfy: false,
      loginForm: {},
      text: '向右滑',

      rules: {
        username: [{
          required: true,
          trigger: 'blur',
          message: '用户名不能为空'
        }],
        code: [{
          required: true,
          trigger: 'change',
          message: '验证码不能为空'
        }],
        cellPhone: [
          {validator: checkPhone, trigger: 'blur'}
        ],
        phone: [
          {validator: checkPhone, trigger: 'blur'}
        ],
        loginpassword: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
        ],
        passwordAgain: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        resetpassword: [
          { validator: validatePass3, trigger: 'blur' },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
        ],
        resetwordAgain: [
          { validator: validatePass4, trigger: 'blur' }
        ],
      },
      loginLoading:false,
      type:'passlogin',               // 展示的状态
      logonform:{                     // 注册数据
        password:''
      },               
      show: true,                     // 初始启用按钮
      count: '',                      // 初始化次数
      timer: null,
      logonLoading:false,             // 注册按钮loading
      passloginForm:{
        rememberMe:false,
      },

      codeloginForm:{},
      codelogintimer: null,
      codelogincount: '',                  // 初始化次数
      codeloginshow: true,                 // 初始启用按钮


      resetpassform:{
        resetpassword:''
      },
      resetLoading:false,
      resetpasstimer: null,
      resetpasscount: '',                  // 初始化次数
      resetpassshow: true,                 // 初始启用按钮
    }
  },
  created () {
    this.getCookie()
  },
  methods: {
    // 登录成功后的存值并跳转
    saveData(obj){
      console.log(obj)
      this.$message({
        message: '登录成功',
        type: 'success'
      });
      this.$store.commit('SET_USER_ID', obj.id)
      localStorage.setItem('userId', obj.id)
      localStorage.setItem('userName', obj.userName)
      localStorage.setItem('shopId', obj.shopId)
      setToken(obj.token, this.type == 'codelogin' ? true : this.loginForm.rememberMe)
      this.$store.commit('SET_TOKEN', obj.token)
      setTimeout(()=>{
        this.loginLoading = false;
        this.$router.push({
          path: '/selectShop'
        })
      },1000)
    },
    // 验证手机号
    checkPhone(value){
      const reg = /^1[3|4|5|7|8|9][0-9]\d{8}$/
      console.log(reg.test(value));
      if (reg.test(value)) {
        return true
      } else {
        return false;
      }
    },
    
    // 拖拽图 - 成功
    onSuccess () {
      this.saveData(this.loginData)
    },
    // 拖拽图 - 失败
    onFail () {
      console.log('错误了')
    },
    // 拖拽图 - 刷新
    onRefresh () {
      console.log('点击了刷新')
    },
    
    getCookie () {
      const phone = Cookies.get('phone')
      let password = Cookies.get('password')
      // 保存cookie里面的加密后的密码
      this.cookiePass = password === undefined ? '' : password
      password = password === undefined ? this.passloginForm.password : password
      this.passloginForm = {
        phone: phone === undefined ? this.passloginForm.phone : phone,
        password: password,
      }
    },
    // 账号登录 | 验证码登录 | 注册 | 忘记密码
    changeType(e){
      this.$refs.passloginForm.clearValidate();
      this.$refs.codeloginForm.clearValidate();
      this.$refs.logonform.clearValidate();
      this.$refs.resetpassform.clearValidate();
      this.type = e
    },
    // 登录 - 密码
    passLoginFun(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginLoading = true;
          this.passloginForm.password = this.passloginForm.loginpassword
          console.log('密码登录请求的数据',this.passloginForm)
          if(this.passloginForm.rememberMe){
            Cookies.set('phone', this.passloginForm.phone, {
              expires: Config.passCookieExpires
            })
            Cookies.set('password', this.passloginForm.password, {
              expires: Config.passCookieExpires
            })
          } else {
            Cookies.remove('phone')
            Cookies.remove('password')
          }
          login(this.passloginForm).then(res=>{
            console.log('登录的res',res)
            if(res.code != 200){
              this.$message.error(res.message);
              this.loginLoading = false;
              return
            }
            let obj = {};
            obj.id = res.data.id
            obj.token = res.data.token
            obj.userName = res.data.userName
            obj.shopId = res.data.shopId
            this.loginData = obj;
            this.codeUrlsfy = true
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 登录 - 获取验证码
    sendLogin(){
      console.log(this.codeloginForm)
      if(!this.checkPhone(this.codeloginForm.phone)){
        this.$message.error('请输入正确的手机号');
        return;
      }
      if (!this.codelogintimer) {
        sendPhoneCode(this.codeloginForm.phone,3).then(res=>{
          console.log('登录获取验证码',res)
          if(res.code == 200){
            this.codelogincount = LOGIN_TIME_COUNT;
            this.codeloginshow = false;
            this.codelogintimer = setInterval(() => {
              if (this.codelogincount > 0 && this.codelogincount <= LOGIN_TIME_COUNT) {
                this.codelogincount--;
              } else {
                this.codeloginshow = true;
                clearInterval(this.codelogintimer);  // 清除定时器
                this.codelogintimer = null;
              }
            }, 1000)
          }else{
            this.$message.error(res.message);
          }
        })
      }
    },
    // 登录 - 验证码登录
    codeLoginFun(formName){
      console.log('点击了验证码登录，数据：',this.codeloginForm)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginLoading = true;
          checkCode(this.codeloginForm.phone,this.codeloginForm.code,3).then(res=>{
            console.log('验证码登录',res)
            if(res.code == 200){
              this.saveData(res.data)
            }else{
              this.$message.error(res.message);
              this.loginLoading = false;
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 注册 - 获取验证码
    send(){
      if(!this.checkPhone(this.logonform.cellPhone)){
        this.$message.error('请输入正确的手机号');
        return;
      }
      if (!this.timer) {
        sendPhoneCode(this.logonform.cellPhone,2).then(res=>{
          console.log('注册获取验证码',res)
          if(res.code == 200){
            this.count = TIME_COUNT;
            this.show = false;
            this.timer = setInterval(() => {
              if (this.count > 0 && this.count <= TIME_COUNT) {
                this.count--;
              } else {
                this.show = true;
                clearInterval(this.timer);  // 清除定时器
                this.timer = null;
              }
            }, 1000)
          }else{
            this.$message.error(res.message);
          }
        })
        
      }
    }, 
    // 注册 - 提交
    handleLogon(formName){
      console.log('点击了注册')
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.logonLoading = true;
          console.log('请求的数据',this.logonform)
          checkCode(this.logonform.cellPhone,this.logonform.code,2).then(res=>{
            console.log('校验验证码',res)
            if(res.code == 200){
              userRegist(this.logonform).then(res1=>{
                console.log('用户注册',res1)
                if(res1.code == 200){
                  this.$message({
                    message: '恭喜你，注册成功',
                    type: 'success'
                  });
                  this.show = true;
                  clearInterval(this.timer);  // 清除定时器
                  this.timer = null;
                  TIME_COUNT = 60
                  setTimeout(()=>{
                    this.type = 'passlogin'
                    this.logonLoading = false;
                  },1000)
                }else{
                  this.logonLoading = false;
                  this.$message.error(res1.message);
                }
              })
            }else{
              this.logonLoading = false;
              this.$message.error(res.message);
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 重置密码 - 获取验证码
    sendreset(){
      console.log(this.codeloginForm)
      if(!this.checkPhone(this.resetpassform.cellPhone)){
        this.$message.error('请输入正确的手机号');
        return;
      }
      if (!this.resetpasstimer) {
        sendPhoneCode(this.resetpassform.cellPhone,1).then(res=>{
          console.log('重置密码获取验证码',res)
          if(res.code == 200){
            this.resetpasscount = RESET_TIME_COUNT;
            this.resetpassshow = false;
            this.resetpasstimer = setInterval(() => {
              if (this.resetpasscount > 0 && this.resetpasscount <= RESET_TIME_COUNT) {
                this.resetpasscount--;
              } else {
                this.resetpassshow = true;
                clearInterval(this.resetpasstimer);  // 清除定时器
                this.resetpasstimer = null;
              }
            }, 1000)
          }else{
            this.$message.error(res.message);
          }
        })
      }
    },
    // 重置密码 - 提交
    handleReset(formName){
      console.log('点击了重置')
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.resetLoading = true;
          console.log('请求的数据',this.resetpassform)
          checkCode(this.resetpassform.cellPhone,this.resetpassform.code,1).then(res=>{
            console.log('校验验证码',res)
            if(res.code == 200){
              editPassWord({cellPhone:this.resetpassform.cellPhone,password:this.resetpassform.resetpassword}).then(res1=>{
                console.log('重置密码',res1)
                if(res1.code == 200){
                  this.$message({
                    message: '恭喜你，重置成功',
                    type: 'success'
                  });
                  this.resetpassshow = true;
                  clearInterval(this.resetpasstimer);  // 清除定时器
                  this.resetpasstimer = null;
                  RESET_TIME_COUNT = 60
                  setTimeout(()=>{
                    this.type = 'passlogin'
                    this.resetLoading = false;
                  },1000)
                }else{
                  this.resetLoading = false;
                  this.$message.error(res1.message);
                }
              })
            }else{
              this.resetLoading = false;
              this.$message.error(res.message);
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  }
}
</script>

<style rel="stylesheet/scss" lang="scss">
.login {
  display: flex;
