import request from '@/utils/request'
// 发送验证码 t = 1 找回密码   t =2 用户注册
export function sendPhoneCode(p,t) {
  return request({
    url: `/user/business/base/sendPhoneCode?phone=${p}&channel=${t}`,
    method: 'get',
  })
}
// 效验验证码  t = 1 找回密码   t =2 用户注册  t = 3 验证码登录
export function checkCode(p,c,t) {
  return request({
    url: `/user/business/base/checkCode?phone=${p}&code=${c}&channel=${t}`,
    method: 'get',
  })
}
// 账号注册
export function userRegist(data) {
  return request({
    url: `/user/business/user/userRegist`,
    method: 'post',
    data: data
  })
}
// 登录
export function login(data) {
  return request({
    url: `/user/business/login`,
    method: 'post',
    data: data
  })
}
// 修改用户密码
export function editPassWord(data) {
  return request({
    url: `/user/business/user/editPassWord`,
    method: 'post',
    data: data
  })
}